.container.grid{
    
    display: flex;
    flex-wrap: wrap;
    padding: 12px;
    position:relative;
    min-height: calc(100vh - 110px);
}
.grid .loading{
    position:absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(18,18,18,0.5);
}
.loading p {
    position:sticky;
    top: 50%;
    text-align: center;
}
